.VideoServiceBanner {
    padding: 50px 0;
}

.VideoServiceBanner .container {
    display: flex;
    align-items: center;
    background: #333;
    padding: 30px;
    border-radius: 20px 60px 20px 20px;
}

.VideoServiceBanner .callToAction {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.VideoServiceBanner .callToAction .title {
    font-size: 31px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 20px;
}

.VideoServiceBanner .callToAction .text {
    font-size: 26px;
    font-weight: 400;
    color: var(--secondary-color);
}

.VideoServiceBanner .callToActionButton {
    font-size: 21px;
    font-weight: 500;
    border-radius: 100px;
    height: 52px;
    padding: 15px 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.VideoServiceBanner .callToActionButton:hover{
    color: #201747;
}

.VideoServiceBanner .videoServiceIcon {
    width: 88px;
    height: 88px;
    border-radius: 45px;
    border: 2px var(--secondary-color) solid;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    flex-shrink: 0;
    margin-right: 20px;
}

.VideoServiceBanner .videoServiceIcon img {
    width: 120%;
}

@media screen and (max-width: 991.8px) {
    .VideoServiceBanner .callToAction .title {
        font-size: 20px;
    }

    .VideoServiceBanner .callToAction .text {
        font-size: 16px;
    }

    .VideoServiceBanner .callToActionButton {
        font-size: 16px;
        border-radius: 28px;
        height: 46px;
        padding: 10px 25px;
    }
}

@media screen and (max-width: 559.8px) {
    .VideoServiceBanner {
        margin: 0 10px;
    }

    .VideoServiceBanner .container {
        flex-direction: column;
        text-align: center;
    }

    .VideoServiceBanner .callToAction {
        flex-direction: column;
        text-align: center;
        margin: 20px 0;
    }
}