.CustomerSlider .ourCustomersTitle {
    color: var(--primary-color);
    text-align: center;
    margin-bottom: 0.938rem;
    font-size: 2.2043rem;
    line-height: 1.25;
    font-weight: 600;
}
.CustomerSlider .Divider {
    margin-bottom: 50px;
}

.CustomerSlider .ourCustomersDescription {
    font-size: .9rem;
    margin-bottom: 50px;
    color: #696969;
    text-align: center;
}

.CustomerSlider .slick-slide {
    margin: 0 10px;
}

@media screen and (max-width: 559.8px) {
    .CustomerSlider .ourCustomersTitle {
        font-size: 1.5rem;
    }
}