.solutionTitle {
    font-size: 30px;
    margin-bottom: 20px;
}

.titleNumber {
    color: var(--primary-color);
    font-size: 30px;
    font-weight: 700;
}

.titleText {
    font-size: 30px;
    color: #484848;
    font-weight: 700;
}

.slideButton {
    font-size: 16px;
    margin-bottom: 30px;
    cursor: pointer;
}

.slideButton.active .buttonNumber {
    color: var(--primary-color);
    font-weight: 700;
}

.slideButton .buttonNumber, .slideButton .buttonText {
    color: #b4b4b4;
}

.slideButton:hover .buttonNumber, .slideButton:hover .buttonText {
    color: #484848;
    font-weight: 700;
}

.slideButton.active .buttonText {
    color: #484848;
    font-weight: 700;
}

.dots {
    display: flex;
}

.slideMenu {
    height: 100%;
    display: flex;
    padding-left: 20px;
    flex-direction: column;
    justify-content: center;
}

.slideDot {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    cursor: pointer;
    background: rgba(96, 10, 179, .3);
    transition: background .3s ease;
}

.solutionSlide {
    position: relative;
    border-top-left-radius: 50px;
    border-bottom-right-radius: 50px;
    overflow: hidden;
}

.solutionSlide img {
    width: 100%;
}

.slideDot.active {
    background: rgb(96, 10, 179);
}

.slideDot:not(:last-child) {
    margin-right: 20px;
}

.SolutionSection1 .item {
    margin-bottom: 50px;
}

.SolutionSection1 .item .itemImage {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
}

.SolutionSection1 .item .itemImage img {
    position: relative;
    height: 500px;
    transition: transform .3s ease;
}
.SolutionSection1 .item .itemImage:hover img {
    transform: scale(1.1);
}


.SolutionSection1 .item.lt .text {
    padding-left: 100px;
}

.SolutionSection1 .item.rt .text  {
    padding-right: 100px;
}

.SolutionSection1 .item .text {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.SolutionSection1 .item .itemTitle {
    text-align: center;
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: var(--primary-color);
    padding: 5px 0;
    border-bottom: 2px solid var(--secondary-color);
}

.SolutionSection1 .item .itemDescription {
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 1.5;
    color: #232323;
    text-align: justify;
}


@media screen and (min-width: 1200px) {
    .titleText {
        display: none;
    }
}