.ProfileItem {
    display: flex;
    margin-bottom: 20px;
    align-items: center;
}

.ProfileItem .icon {
    flex-shrink: 0;
    margin-right: 20px;
}

.ProfileItem .content {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.ProfileItem .itemTitle {
    font-size: 20px;
    margin-bottom: 10px;
    line-height: 1em;
    font-weight: 500;
    color: var(--primary-color);
}

.ProfileItem .itemDescription {
    font-size: 14px;
    color: #666;
    line-height: 1.7em;
    font-weight: 500;
}