.VideoServiceForm {
    background: #333;
}

.VideoServiceForm .container {
    margin-top: 50px;
    border-radius: 10px;
    padding: 10px;
    border: 3px solid var(--primary-color);
    padding: 20px;
}

.VideoServiceForm p {
    color: #fff;
    margin-bottom: 20px;
}

.VideoServiceForm .icon {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.VideoServiceForm .buttons {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
}

.VideoServiceForm .buttons .btnStart {
    flex: 1;
    margin-right: 20px;
}

.VideoServiceForm .icon img {
    width: 150px;
}