.StepItem {
    display: flex;
    flex-direction: column;
    position: relative;
}

.StepItem .stepWrapper {
    display: flex;
    align-items: center;
}

.StepItem.lt .stepWrapper {
    flex-direction: row;
}

.StepItem.rt .stepWrapper {
    flex-direction: row-reverse;
}

.StepItem.lt .icon {
    margin-right: 1.5rem;
}
.StepItem.rt .icon {
    margin-left: 1.5rem;
}

.StepItem .icon {
    width: 100px;
    height: 100px;
    border-radius: 70px;
    box-shadow: 0 0 2.1875rem 0 rgba(119, 119, 120, 0.13);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    flex-shrink: 0;
    transition: transform .3s ease-in-out;
}

.StepItem .icon:hover {
    transform: scale(1.2);
}

.StepItem .textContent h5 {
    font-size: 1.1725rem;
    line-height: 1.25;
    color: var(--primary-color);
    margin-bottom: 0.938rem;
}

.StepItem .textContent p {
    color: #696969;
}

.StepItem img {
    width: 200px;
}

.StepItem .path {
    overflow: hidden;
}

.StepItem.lt .path {
    display: flex;
    justify-content: flex-start;
}

.StepItem.rt .path {
    display: flex;
    justify-content: flex-end;
}

.StepItem.rt .path img {
    margin-right: 40px;
}

.StepItem.lt .path img {
    margin-left: 50px;
}

@media screen and (max-width: 579.8px)  {
    .StepItem .textContent h5 {
        font-size: 1rem;
    }

    .StepItem .icon {
        width: 80px;
        height: 80px;
    }
}