.ListItem {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 20px;
}

.ListItem .icon {
    display: inline-block;
    margin-right: 10px;
}

.listText {
    font-size: 1.1rem;
    text-align: justify;
}

.listText.dark {
    color: #f8f9fa;
}