.Question {
    display: flex;
    flex-direction: column;
}

.questionHeader {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.33;
    background-color: #FFFFFF;
    cursor: pointer;
    padding: 15px 10px;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);;
}

.questionTitle {
    flex: 1;
    color: #232323;
}

.buttonCollapse {
    background: transparent;
    width: 30px;
    height: 30px;
    text-align: center;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.answerWrapper {
    color: #232323;
    overflow: hidden;
    text-align: justify;
    height: auto;
    max-height: 500px;
    font-size: 1rem;
    line-height: 1.5;
    transition: max-height .5s ease-in-out;
}

.answerWrapper span {
    display: inline-block;
    padding: 10px;
}

.answerWrapper.hidden {
    max-height: 0;
}