.StepBox {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
}

.StepBox .indicatorWrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
}

.StepBox .stepIndicator {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    color: #fff;
    background: var(--primary-color);
    display: inline-flex;
    justify-content: center;
    font-weight: 700;
    font-size: 1.2rem;
    align-items: center;
}

.StepBox .stepContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 30px;
    box-shadow: 1px 4px 10px rgba(0, 0, 0, .03);
    min-height: 250px;
    padding: 40px 60px;
}

.StepBox .stepTitle {
    font-size: 1.3rem;
    margin-bottom: 16px;
    text-align: center;
}

.StepBox .stepText {
    line-height: 1.6;
    text-align: center;
}