.Tab {
    display: inline-flex;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    background: #fff;
    text-align: center;
    transition: background .3s ease;
}

.Tab.active {
    background: var(--primary-color);
    color: #fff;
}

.Tab:hover {
    background: transparent;
    color: #fff;
}

.Tab .title {
    margin-left: 10px;
    color: #333;
    text-align: center;
    text-transform: uppercase;
    transition: color .3s ease;
    font-weight: 600;
}

.Tab.active .title {
    color: #fff;
}

.Tab:hover .title, .Tab:hover .icon {
    color: #fff;
}