.SocialResponsibility{
    position: relative;
    min-height: 500px;
}

.SocialResponsibility .Content {
    position: absolute;
    left: 25%;
    right: 25%;
    bottom: -174px;
    z-index: 50;
    min-height: 280px;
    border: 4px var(--primary-color) solid;
}

.SocialResponsibility .Content .call-to-action {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.SocialResponsibility .Content .call-to-action .image {
    width: 150px;
    margin-bottom: 20px;
}

.SocialResponsibility .ButtonLink {
    width: 300px;
    height: 50px;
    font-size: 1rem;
    color: #333;
}

.SocialResponsibility .ButtonLink:hover {
    color: #fff;
}

.socialResponsibilityHeading {
    margin-bottom: 20px;
}

.socialResponsibilityText {
    font-size: 32px;
    color: #303040;
    letter-spacing: 1px;
    margin-bottom: 20px;
    line-height: 120%;
    font-weight: 600;
    text-align: center;
    text-align: justify;
}

.SocialResponsibility .buttonWrapper {
    display: flex;
    justify-content: flex-start;
}

.socialResponsibilityThumb img {
    width: 100%;
    transition: transform .5s ease-in-out;
}

.socialResponsibilityThumb:hover img {
    transform: scale(1.1);
}

@media screen and (max-width: 991.8px) {
    .SocialResponsibility {
        flex-direction: column;
    }

    .SocialResponsibility .Content {
        width: 600px;
        left: calc(50% - 300px);
    }

    .socialResponsibilityText {
        font-size: 24px;
    }

    .SocialResponsibility .socialResponsibilityThumb {
        max-height: 350px;
    }

    .socialResponsibilityHeading {
        text-align: center;
    }

    .SocialResponsibility .buttonWrapper {
        justify-content: center;
    }
}

@media screen and (max-width: 767.8px) {
    .SocialResponsibility .Content {
        width: 420px;
        left: calc(50% - 210px);
    }

    .SocialResponsibility .Content .call-to-action .image {
        width: 100px;
    }

    .socialResponsibilityText {
        font-size: 20px;
    }
}

@media screen and (max-width: 439.8px) {
    .SocialResponsibility .Content {
        width: 350px;
        left: calc(50% - 175px);
    }
}