.PriceCard {
    background: #2c2c31;;
    color: #fff;
    padding: 20px;
    border-radius: 10px;
    border: 4px solid transparent;
    transform: translateY(0);
    transition: border-color .3s ease, transform .3s ease-in-out;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, .05);
}

.PriceCard:hover {
    border-color: var(--primary-color);
    transform: translateY(-10px);
}

.PriceCard .ButtonLink {
    width: 100%;
    font-size: 1.2rem;
}

.PriceCard .value {
    font-size: 2rem;
    color: var(--secondary-color);
    text-align: center;
    font-weight: 700;
    margin-bottom: 20px;
}

.PriceCard .cardTitle {
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    padding-bottom: 30px;
    text-align: center;
}

.PriceCard .cardDescription {
    text-align: center;
    margin-bottom: 20px;
}


.service {
    display: flex;
    flex-direction: column;
}

.service .itemTitle {
    font-weight: 600;
    border-bottom: 4px solid var(--secondary-color);
    padding: 10px 0;
    text-align: center;
}

.serviceItem {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    padding: 5px 0;
    padding-left: 20px;
}

.serviceItem .description {
    text-align: left;
    font-size: 14px;
    line-height: 20px;
    flex: 1;
}

.serviceItem .price {
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    flex-shrink: 0;
}

@media screen and (max-width: 991.8px) {
    .PriceCard {
        margin-bottom: 40px;
        min-height: auto;
    }
}