.ProfileOfOurFranchisees {
    position: relative;
}

.ProfileOfOurFranchisees .circle {
    width: 500px;
    height: 500px;
    border-radius: 250px;
    z-index: -1;
    right: -250px;
    top: calc(50% - 250px);
    background: var(--primary-color);
    position: absolute;
}

@media screen and (max-width: 991.8px) {
    .ProfileOfOurFranchisees .circle {
        display: none;
    }
}