.HowToProtestACostumer {
    padding: 50px 0;
}

.HowToProtestACostumer .steps {
    height: 100%;
}

.HowToProtestACostumer .slick-slider {
    margin-top: 100px;
}

.HowToProtestACostumer .image {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.HowToProtestACostumer .image img {
    width: 100%;
}

.slick-slider {
    z-index: 1;
}

.slick-next {
    right: -20px;
}
.slick-prev {
    left: -40px;
}

.slick-next::before, .slick-prev::before {
    color: #333;
    font-size: 40px;
}