.Indicators {
    padding: 50px 0;
    min-height: auto;
}

.Indicators .row {
    justify-content: center;
}

.Indicators .indicator {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    margin-bottom: 10px;
}

.indicator .title {
    font-weight: 700;
    font-size: 2.5rem;
    line-height: 1.2;
    color: #fff;
    text-align: center;
}

.indicator .subtitle {
    text-align: center;
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 1.5;
    color: #fff;
}