.HowMuchDoesPixCost {
    background: var(--primary-color);
}

.HowMuchDoesPixCost .heading {
    color: #fff;
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: 30px;
}

.HowMuchDoesPixCost table {
    border: none;
    width: 100%;
    border-collapse: collapse;
    color: #fff;
    margin-bottom: 40px;
}

.HowMuchDoesPixCost table thead th {
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1.33;
    align-items: center;
    align-self: center;
    padding: 5px 20px;
}

.HowMuchDoesPixCost .personColumn {
    width: 25%;
}

.HowMuchDoesPixCost .featureTitle {
    padding: 20px;
    text-align: left;
    vertical-align: middle;
    width: 50%;
}

.HowMuchDoesPixCost .itemValue {
    text-align: center;
    vertical-align: middle;
    padding: 20px 0;
    line-height: 1.8;
}

.HowMuchDoesPixCost .borderBottom {
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}