.OurCustomers {
    padding-top: 200px;
    padding-bottom: 50px;
    min-height: 200px;
    position: relative;
}

.OurCustomers .indicator {
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background: rgba(34,34,34,.4);
    display: inline-block;
    cursor: pointer;
}

.OurCustomers .CustomerSlider {
    margin-top: 30px;
}

.OurCustomers .slick-slider {
    max-height: 100px;
}

.OurCustomers .indicator.active {
    background: #222;
}

.OurCustomers .indicator:not(:last-child) {
    margin-right: 12px;
}

.OurCustomers img.image {
    width: 64px;
    margin-right: 10px;
}


.OurCustomers .quoteIcon {
    position: absolute;
    top: -50px;
}

