.TabContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.TabContent .ButtonLink {
    width: 300px;
    height: 50px;
    font-size: 1rem;
    color: #333;
}

.TabContent .ButtonLink:hover {
    color: #fff;
}

.TabContent .title {
    font-weight: 700;
    font-size: 1.8rem;
    line-height: 1.33;
    margin: 20px 0;
    text-align: center;
}

.TabContent .text {
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 1.5;
    margin-bottom: 30px;
    text-align: center;
}