.CustomTabs .header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
}

.CustomTabs .tabs:hover {
    background-color: var(--primary-color);
}

.CustomTabs .tabs {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, .1);
    height: 50px;
    border-radius: 25px;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    background-color: #fff;
}

.CustomTabs .content {
    display: flex;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    min-height: 400px;
}

.CustomTabs .content .contentWrapper {
    width: 100%;
    position: absolute;
    transition: all 0.5s;
}

.CustomTabs .content .contentWrapper .TabContent {
    width: 100%;
}