.CustomStepCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0;
}

.CustomStepCard .icon {
    width: 100px;
    height: 100px;
    border-radius: 70px;
    box-shadow: 0 0 2.1875rem 0 rgba(119, 119, 120, 0.13);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    flex-shrink: 0;
    transition: transform .3s ease-in-out;
    margin-bottom: 1.5rem;
}

.CustomStepCard .icon:hover {
    transform: scale(1.2);
}

.CustomStepCard h5 {
    font-size: 1.1725rem;
    line-height: 1.25;
    color: var(--primary-color);
    margin-bottom: 0.938rem;
    text-align: center;
}

.CustomStepCard p {
    color: #696969;
    text-align: center;
}